@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Quicksand:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Urbanist:wght@100&display=swap');
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}
body{
  font-family: "Urbanist";
}

html, body{
  scroll-behavior: smooth;
  transition: 0.3s ease-in-out;
}
section {
  height: 40vh;
  width: 100%;
  background-color: mediumseagreen;
  position: absolute;
}

section img {
  object-fit: contain;
  height: 23%;
  width: 14%;
  margin-left: 1%;
  margin-top: 3%;
}
.sec1 {
  height: 100vh;
  max-width: 100%;
  /* background: #F2F2FC; */
  background: white;
  display: flex;
  z-index: 2;
  overflow: hidden;
}
.right,
.left {
  width: 50%;
  height: 100vh;
  z-index: 1;
}
.sec1 .right {
  display: flex;
  justify-content: end;
  padding-top: 8%;
}
.right img {
  height: 98%;
  width: 59%;
}
.left {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  font-family: "Ek Mukta", sans-serif;
  flex-direction: column;
  padding-left: 10%;
  padding-top: 10%;
}
.left h1 {
  font-size: 3rem;
  letter-spacing: 1px;
}
.left p {
  font-size: 2rem;
}
.left button {
  border: none;
  padding: 1.5%;
  background: mediumseagreen;
  width: 25%;
  font-size: 1.2rem;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  color: white;
}

.hide{
  height: 50vh;
  width: 50%;
}

.left button:hover{

    box-shadow: inset 200px 0 0 0 black;
    color: white;
  
}
.news1 button{
  flex: 1;
background-color: teal;
color: white;
border: none;
outline: none;
cursor: pointer;
}

.news1 input{
border: none;
flex: 8;
padding: 0px 20px;
outline: none;
background-color: #3CB371;
}

.news1{
  width: 100%;
height: 40vh;
background-color: white;
display: flex;
justify-content: space-between;
border: 0.1px solid lightgray;
}

.news1 h1{
  width: 100%;
  height: 27vh;
  background-color: white;
  display: flex;
  justify-content: space-between;
  border: 0.1px solid lightgray;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10%;
  padding: 5% 0 5% 0;
}

.news1 .desciption{
  font-size: 24px;
font-weight: 300;
margin-bottom: 20px;
}

.nav{
height: 60vh;
background-color:  #262B2F ;
color:  #E5E5E5 ;
display: flex;
justify-content: center;
align-items: center;
transition:  1s ease-in-out;
}

.registeration{
  display: flex;
}

.loginregis{
  display: flex;
}
.regisimg, .loginimg{
  width: 50%;
  height: 100vh;
}


.regisimg img{
  height: 100%;
  width: 100%;
  background: teal;
  padding-top: 6%;
}
.loginimg img{
height: 81%;
    width: 100%;
    background: white;
    padding-top: 6%;
    object-fit: contain;
}

.nav {
  height: 10vh;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
padding:0 0 0 5%;
position: fixed;
z-index: 5;
top:0;
color:white;
width: 100%;
}

.nav2{
  background-color: #3CB371;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
padding:0 0 0 5%;
position: fixed;
z-index: 5;
color:white;
width: 100%;
}

.nav img,.nav2 img {
  height: 65%;
  width: 20%;
  object-fit: contain;

}

.nav ul, .nav2 ul {
  display: flex;
  width: 35%;
  justify-content: space-around;
  font-size: 1.4rem;

}
li,a {
  text-decoration: none;
  list-style-type: none;
  transition: 0.5s ease-in-out;
  background-position-x: right;
  border-bottom: 2px solid transparent;
  border-width: 0%;
  position: relative;
  cursor: pointer;
  color: whitesmoke;
  font-weight :600;
  letter-spacing: 1px;
}

li::after {
  content: " ";
  width: 0%;
  background-color: rgb(252, 252, 252);
  position: absolute;
  height: 2.5px;
  left: 0;
  bottom: -7px;
  transition: 0.3s linear;
}
li:hover::after{
 width: 100%;
}
.sec2{
  height: 100vh;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #F4F4FC;
  z-index: 100;
}

.openmenu, .closemenu{
  display: none;
}
video{
  width: 50%;
  height: 80vh;
}
.regiscolor {
  font-weight: 100;
  font-size: 1.2rem;
  border-bottom: 1px solid black;
  padding: 10px;
}

svg{
  position: absolute;
}
.phone {
  display: flex;
  height: 7vh;
  width: 20%;
  position: absolute;
  bottom: 11%;
  justify-content: space-between;
  align-items: center;
}
.phone span{
  font-size: 25px;
}
.container {
  width: 50%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 6vh;
  background-size: cover;
  background-position: right;
  background-color: rgba(0,0,0,0.2);
  background-blend-mode: multiply;
  flex-direction: column;
  background: teal;
}

.container2 {
  width: 50%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 6vh;
  background-size: cover;
  background-position: right;
  background-blend-mode: multiply;
  flex-direction: column;
  background: #1e1879;
}


