}

.no-nav{
  height: 10vh;
  display: flex;
  width: 36%;
  justify-content: space-between;
  gap: 5%;
  font-size: 1.2rem;
  align-items: center;

}

nav .no-nav{
  background-color: #262B2F;
  height: 100vh;
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  top: 0;
  justify-content: center;
  width: 100%;
  transform: translateY(-100vh);
  transition: 800ms ease-in-out;
}

nav svg{
  position: relative;
}
nav .Nav {
  background-color: #262B2F;
  height: 100vh;
  display: flex;
